<template>
    <div>
        预警灯配置
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
             <el-form-item  label="活动性质" prop="type">
                <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
                    <el-table-column type="selection" width="55">
                    </el-table-column>
                    <el-table-column label="日期" width="120">
                        <template slot-scope="scope">{{ scope.row.date }}</template>
                    </el-table-column>
                    <el-table-column prop="name" label="姓名" width="120">
                    </el-table-column>
                    <el-table-column prop="address" label="地址" show-overflow-tooltip>
                        <template slot-scope="scope">
                            <img :src="scope.row.photo" width="80" height="40" alt="..." />
                        </template>
                    </el-table-column>
                </el-table>
            </el-form-item>
            <el-form-item style="text-align:right;margin-right:30px">
                    <el-button @click="centerDialogVisiblePhotoImg = false">取 消</el-button>
                    <el-button type="primary" @click="removeDomainEnt">确 定</el-button>
             
            </el-form-item>

        </el-form>

    </div>
</template>

<script>
export default {
    data() {
        return {
            ruleForm: {
                type: [],
                region: ''
            },
            rules: {
                type: [
                    { type: 'array', required: true, message: '请至少选择一个活动性质', trigger: 'change' }
                ],
                region: [
                    { required: true, message: '请选择活动区域', trigger: 'change' }
                ],
            },

            options: [{
                value: 'HTML',
                label: 'HTML'
            }, {
                value: 'CSS',
                label: 'CSS'
            }, {
                value: 'JavaScript',
                label: 'JavaScript'
            }],
            value: [],

            //弹层数据
            centerDialogVisiblePhotoImg: false,
            tableData: [{
                date: '2016-05-03',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
            }, {
                date: '2016-05-02',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
            }, {
                date: '2016-05-04',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
            }, {
                date: '2016-05-01',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
            }, {
                date: '2016-05-08',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
            }, {
                date: '2016-05-06',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
            }, {
                date: '2016-05-07',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
            }],
            multipleSelection: []
        }
    },
    methods: {
        materialSel() {
            this.centerDialogVisiblePhotoImg = true
        },

        handleSelectionChange() {
            console.log(111);
        },
        //
        removeDomainEnt() {
            console.log("确定按钮");
        }


    }
}
</script>

<style>
</style>


